.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
p{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: #ffebee; 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
h1{
  display: flex;
  gap: 10px;
}
.valentines-container {
  text-align: center;
  background-color: #fff;
  padding: 40px;
  border-radius: 5px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}
.heart {
  font-size: 30px;
  color: #570000; 
  animation: beat 1s infinite; 
}

@keyframes beat {
  0% {
    transform: scale(1); 
  }
  50% {
    transform: scale(1.3); 
  }
  100% {
    transform: scale(1);
  }
}
.valentines-button{
  background-color: rgb(218, 121, 139);
  border-radius: 30px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
  border-color: transparent;
  transition: background-color 0.3s ease;
  text-align: center;
  justify-content: center;
  width: 70px;

  height: 40px;
}
.valentines-button:hover {
  background-color: #c2185b; /* Darker pink on hover */
}
.v-div{
  gap: 20px;
  display: flex;
  justify-content: center;
}

.app {
  position: relative;
  overflow: hidden;
}

.cycle-gif {
  position: absolute;
  top: 25px; 
  
  right: -250px;
  width: 100px ;
  
  height: 80px;
  animation: moveCycle 6s linear infinite;
  border-radius: 1500px;
}

@keyframes moveCycle {
  0% {
    right: -250px;
    
  }
  100% {
    right: 100vw;
  }
}

.valentines-container {
  position: relative;
  text-align: center;
  margin-top: 100px;
}
